import PropTypes from 'prop-types';
import React from 'react';
import TodoTextInput from '../components/TodoTextInput';
import ReactSVG from 'react-svg';
import UndoRedo from '../containers/UndoRedo';

const Header = ({addTodo, completeAllTodos}) => (
  <header className='header'>
    <button
      className='button header__toggle'
      onClick={completeAllTodos}
    >
      <ReactSVG path='https://gist.githubusercontent.com/martonlanga/994113538a28d2bcadb708a0303c90be/raw/97b309033cfda5b1c211c3a4412ad424f38856bf/done_all.svg' />
    </button>
    <TodoTextInput
      newTodo
      onSave={(text) => {
        if (text.length !== 0) {
          addTodo(text);
        }
      }}
      placeholder='todo'
    />
    <UndoRedo />
  </header>
);

Header.propTypes = {
  addTodo: PropTypes.func.isRequired,
  completeAllTodos: PropTypes.func.isRequired,
};

export default Header;



